const New = ({ article }) => {

    const { urlToImage, url, title, description, source } = article;

    let img = '';

    if (urlToImage !== '') {
        img = <img className='card-img-top' href={urlToImage} alt={'thumbnails'} />
    }else{
        img = <p>No image for this article</p>
    }

    return (
        <div className='col-md-6 col-12 mb-4'>
            <div className='card'>
                <div className='card-image'>
                    {img}
                </div>
                <div className='card-body'>
                    <h3 className='card-title'>{title}</h3>
                    <p className='card-text'>{description}</p>
                    <p className='card-text'>{source.name}</p>
                    <a href={url} target='_blank' className='btn btn-primary d-block'>Read more...</a>
                </div>
            </div>
        </div>
    );
}

export default New;